//Token
import { fetchToken, fetchAutority } from "../api/index.js";
//Member
import { postSignup, patchChangePwd, getUserArea, postLogin, getFindId, getFindPwd, getCheckNick, getCheckEmail, } from "../api/index.js";
//Notice
import { getNoticeList, postNotice, getNoticeDetail } from "../api/index.js";
//Users
import { getMyPost, getAttendPost, patchUserInfo } from "../api/index.js";
//Delivery
import { getDeliveryList, getSortDeliveryList, postDeliveryPost, patchDeliveryPost, deleteDeliveryPost, getDeliveryPostDetail } from "../api/index.js";
//Chat
import { getPostIdList, getPostInfoList, getNickList, getChatList, postChatData, deleteChatData  } from "../api/index.js";
//RoomInfo
import { postRoomInfo, patchRoomInfo, deleteRoomInfo } from "../api/index.js";

import { router } from '../routes/index.js';
import { store } from "./store.js";
import Swal from 'sweetalert2';

export default{ 

  ////////////////////////// TOKEN //////////////////////////

  FETCH_USER({commit}){
    console.log("");
    console.log(localStorage.getItem('jwt'));
    if(localStorage.getItem('jwt') == null){
      console.log("API:FETCH_USER\n  ()");
      alert("  ");
      router.replace("/home");
      return;
    }else{
      return fetchToken()
      .then(response =>{
        console.log("API:FETCH_USER\n  ",response.data);
        const member = {
          nick: response.data.nick,
          email: "test",
          pw: "test",
          area: response.data.area
        }
        commit('SET_MEMBER',member)
      })
      .catch(error => {
        const code = error.response.status;
        if(code == 403){
          console.log("API:FETCH_USER\n  ( )",error);
          alert("FETCH_USER   ");
          router.replace("/home");
        }
        else{
          console.log("API:FETCH_USER\n  (??)",error);
        }
      })
    }
  },

  FETCH_AUTHORITY({commit}){
    fetchAutority()
      .then(response =>{
        console.log("API:FETCH_AUTHORITY\n  ",response.data);
        commit('SET_AUTHORITY', response.data);
      })
      .catch(error => {
        const code = error.response.status;
        console.log("API:FETCH_AUTHORITY\n  ",error);
        if(code == 403){
          // console.log("API:FETCH_AUTHORITY\n  ( )",error);
          // alert("FETCH_USER   ");
          // router.replace("/home");
        }
        else{
          // console.log("API:FETCH_AUTHORITY\n  (??)",error);
          // console.log("     500 ?");
        }
      });
  },

  ////////////////////////// MEMBER //////////////////////////

  // 
  POST_MEMBER(context, member) {
    return postSignup(member)
      .then(response => {
        if (response.data == "SUCCESS") {
          console.log('API:POST_MEMBER\n ', response.data);
          router.replace({
            name: "signupCompleted",
            query: { nickName: member.nick, }
          });
        }
        else {
          console.log('API:POST_MEMBER\n ', response.data);
          alert(" ");
        }
      })
      .catch(error => { console.log('API:POST_MEMBER\n ', error); })
  },

  //  
  CHANGE_PWD({ commit }, memberDTO) {
    return patchChangePwd(memberDTO)
      .then(response => {
        console.log('API:CHANGE_PWD\n  ', response);
        commit('SET_PWCHANGE_DONE', response.data);
      })
      .catch(error => {
        console.log('API:CHANGE_PWD\n  ', error);
      })
  },

  // 
  POST_LOGIN({ commit }, loginMember) {
    return postLogin(loginMember)
      .then(response => {
        console.log('API:POST_LOGIN\n ', response);

        localStorage.setItem('jwt', response.data); //   

        return getUserArea(loginMember.nick)
          .then(response => {
            console.log("   !", response.data);
            const member = {
              nick: loginMember.nick,
              email: response.data.email,
              pw: "",
              area: response.data.area
            }
            commit('SET_MEMBER', member);
            commit('SET_DELIVERY_AREA', member.area);
            router.replace("/home");
            store.dispatch("FETCH_AUTHORITY");
            store.dispatch('FIND_POST_ID', loginMember.nick);
          })
          .catch(error => {
            router.replace("/home");
            console.log("  ", error);
          })
        })
        .catch(error => {
          const code = error.response.status;
          if (code == 400) {
            console.log("API:POST_LOGIN\n  -   ", error);
            alert("API:POST_LOGIN\n  -   .");
          }
          else if (code == 404) {
            console.log("API:POST_LOGIN\n  -  ", error);
            alert("  .");
          }
          else { console.log("API:POST_LOGIN\n ", error) }
        })
  },

  // FETCH_AREA({ commit }, nick) {
  //   return getUserArea(nick)
  //   .then(response => {
  //     console.log("   !", response.data);
  //     const member = {
  //       nick: nick,
  //       email: response.data.email,
  //       pw: "",
  //       area: response.data.area
  //     }
  //     commit('SET_MEMBER', member);
  //     commit('SET_DELIVERY_AREA', member.area);
  //     router.replace("/home");
  //     store.dispatch("FETCH_AUTHORITY");
  //     store.dispatch('FIND_POST_ID', nick);
  //   })
  //   .catch(error => {
  //     router.replace("/home");
  //     console.log("  ", error);
  //   })
  // },

  //  
  FIND_NICK({ commit }, email) {
    return getFindId(email)
      .then(response => {
        console.log('API:FIND_NICK\n ', response);
        commit('SET_FIND_MEMBER_NICK', response.data)
      })
      .catch(error => { console.log('API:FIND_NICK\n ', error); })
  },

  //  
  FIND_PWD({ commit }, email) {
    return getFindPwd(email)
      .then(response => {
        console.log('API:FIND_PWD\n  ', response);
        commit('SET_FIND_MEMBER_EMAIL', response.data)
      })
      .catch(error => {
        console.log('API:FIND_PWD\n  ', error);
      })
  },

  //  
  POST_NICK({ commit }, nick) {
    return getCheckNick(nick)
      .then(response => {
        console.log('API:POST_NICK\n ', response);
        commit('SET_DUPLI_NICK', false);
      })
      .catch(error => {
        console.log('API:POST_NICK\n ()', error);
        commit('SET_DUPLI_NICK', true)
      })
  },

  //  
  POST_EMAIL({ commit }, email) {
    return getCheckEmail(email)
      .then(response => {
        console.log('API:POST_EMAIL\n ', response);
        commit('SET_DUPLI_EMAIL', false)
      })
      .catch(error => {
        console.log('API:POST_EMAIL\n ', error);
        commit('SET_DUPLI_EMAIL', true)
      })
  },

  ////////////////////////// NOTICE //////////////////////////

  //     
  // axios.get  new Promise  then, catch   .
  //  async/await   .
  FETCH_NOTICE_LIST({ commit }) {
    return getNoticeList()
      .then(response => {
        console.log("API:FETCH_NOTICE_LIST\n   ", response.data);
        commit('SET_NOTICELIST', response.data);
      })
      .catch(error => {
        console.log("API:FETCH_NOTICE_LIST\n      ", error);
      })
  },

  //  
  POST_NOTICE(content, noticeDTO) {
    return postNotice(noticeDTO)
      .then(response => {
        console.log("API:POST_NOTICE\n  ", response);
        Swal.fire({
          icon: 'success',
          title: ' .',
          confirmButtonText: ' ',
        }).then(() => {
          router.replace("/notice/post");
        })
      })
      .catch(error => {
        console.log("API:POST_NOTICE\n  ", error);
      })
  },

  //  
  FETCH_NOTICE_DETAIL({ commit }, notice_id) {
    return getNoticeDetail(notice_id)
      .then(response => {
        console.log("API:FETCH_NOTICE_DETAIL\n   ", response.data);
        commit('SET_NOTICE', response.data);
      })
      .catch(error => {
        console.log("API:FETCH_NOTICE_DETAIL\n     ", error);
      })
  },

  ////////////////////////// USERS //////////////////////////

  //   
  FETCH_MY_DPOST({ commit }, nick) {
    return getMyPost(nick)
      .then(response => {
        console.log('API:FETCH_MY_DPOST\n   ', response);
        commit('SET_MY_DPOST', response.data);
      })
      .catch(error => {
        console.log('API:FETCH_MY_DPOST\n   ', error);
      })
  },

  //   
  FETCH_MY_PARTICI({ commit }, nick) {
    return getAttendPost(nick)
      .then(response => {
        console.log('API:FETCH_MY_PARTICI\n   ', response);
        commit('SET_MY_PARTICI', response.data);
      })
      .catch(error => {
        console.log('API:FETCH_MY_PARTICI\n   ', error);
      })
  },

  //   
  CHANGE_MEMBER({ commit }, memberDTO) {
    return patchUserInfo(memberDTO)
      .then(response => {
        console.log('API:CHANGE_MEMBER\n  ', response);
        commit('SET_MEMBER_CHANGE_DONE', response.data);
      })
      .catch(error => {
        console.log('API:CHANGE_MEMBER\n  ', error);
      })
  },

  ////////////////////////// DELIVERY //////////////////////////

  //    
  FETCH_DELIVERY_LIST({ commit }, area) {
    return getDeliveryList(area)
      .then(response => {
        console.log("API:FETCH_DELIVERY_LIST\n      ", response.data);
        commit('SET_DELIVERIES', response.data);
        if (response == null) {
          alert("null    ");
          router.replace("/home");
        }
      })
      .catch(error => {
        const code = error.response.status;
        if (code == 405) {
          alert("    ");
          router.replace("/home");
        }
        else {
          console.log("API:FETCH_DELIVERY_LIST\n      ", error);
        }
      })
  },
  //     
  FETCH_DELIVERY_LIST_SORT({ commit }, sortDTO) {
    return getSortDeliveryList(sortDTO)
      .then(response => {
        console.log("API:FETCH_DELIVERY_LIST_CATEGORY\n()       \n", response.data);
        commit('SET_DELIVERIES', response.data);
      })
      .catch(error => {
        const code = error.response.status;
        if (code == 403) {
          alert("FETCH_DELIVERY_LIST_CATEGORY   ");
          router.replace("/home");
        }
        else {
          console.log("API:FETCH_DELIVERY_LIST_CATEGORY\n       \n", error);
        }
      })
  },
  //  
  POST_DELIVERY_POST(content, post) {
    return postDeliveryPost(post)
      .then(response => {
        console.log("API:POST_DELIVERY_POST\n  ", response);
        console.log(response.data);
        store.dispatch("FIND_POST_ID", store.state.member.nick);

        Swal.fire({
          icon: 'success',
          title: ' .',
          confirmButtonText: '   ',
        }).then(() => {
          router.replace("/delivery/post");
        });
      })
      .catch(error => {
        const code = error.response.status;
        if (code == 403) {
          alert("POST_DELIVERY_POST   ");
          router.replace("/home");
        }
        else {
          console.log("API:POST_DELIVERY_POST\n  ", error);
        }
      })
  },

  //  
  AMEND_DELIVERY_POST(content, post) {
    return patchDeliveryPost(post)
      .then(response => {
        console.log("API:AMEND_DELIVERY_POST\n  ", response);
      })
      .catch(error => {
        const code = error.response.status;
        if (code == 403) {
          alert("POST_DELIVERY_POST   ");
          router.replace("/home");
        }
        else {
          console.log("API:POST_DELIVERY_POST\n  ", error);
        }
      })
  },

  //  :    ->    ->         
  DELETE_DELIVERY_POST(content, post_id) {
    return deleteChatData(post_id)
      .then(response => {
        console.log('API:DELETE_DELIVERY_POST\n   ', response);

        return deleteRoomInfo(post_id)
          .then(response => {
            console.log('API:DELETE_DELIVERY_POST\n   ', response);

            return deleteDeliveryPost(post_id)
              .then(response => {
                console.log('API:DELETE_DELIVERY_POST\n  ', response);
                store.dispatch('FIND_POST_ID', store.state.member.nick);
              })
              .catch(error => {
                console.log('API:DELETE_DELIVERY_POST\n  ', error);
              })
          })
          .catch(error => {
            console.log('API:DELETE_DELIVERY_POST\n   ', error);
          })
      })
      .catch(error => {
        console.log('API:DELETE_DELIVERY_POST\n   ', error);
      })
  },

  //    
  FETCH_DELIVERY_DETAIL({ commit }, post_id) {
    return getDeliveryPostDetail(post_id)
      .then(response => {
        console.log("API:FETCH_DELIVERY_DETAIL\n   ", response.data);
        commit('SET_DELIVERY_POST', response.data);
      })
      .catch(error => {
        const code = error.response.status;
        if (code == 403) {
          console.log("API:FETCH_DELIVERY_DETAIL\n    -  ", error);
          alert("FETCH_DELIVERY_DETAIL   ");
          router.replace("/home");
        }
        else {
          console.log("API:FETCH_DELIVERY_DETAIL\n   ", error);
        }
      })
  },

  ////////////////////////// CHAT //////////////////////////

  FIND_POST_ID({ commit }, nick) {
    return getPostIdList(nick)
      .then(response => {
        console.log("API:SET_FIND_POSTID  POST_ID   ", response.data);
        commit('SET_FIND_POSTID', response.data);

        if (Object.keys(response.data).length != 0) {
          store.dispatch('FIND_ROOM', response.data);
        }
        else {
          store.state.postIdList = [0];
          store.state.roomList = [{ post_id: 0, post_name: " " }];
        }
      })
      .catch(error => {
        console.log(" POST_ID   ", error);
      })
  },

  FIND_ROOM({ commit }, list) {
    return getPostInfoList(list)
      .then(response => {
        console.log("API:SET_FIND_ROOM  Room   ", response.data);
        commit('SET_FIND_ROOM', response.data);
      })
      .catch(error => {
        console.log(" Room   ", error);
      })
  },

  FIND_USER({ commit }, post_id) {
    return getNickList(post_id)
      .then(response => {
        console.log("API:SET_FIND_USER    nick   ", response.data);
        commit('SET_FIND_USER', response.data);
      })
      .catch(error => {
        console.log("   nick   ", error);
      })
  },

  FIND_CHAT_LOGS({ commit }, post_id) {
    return getChatList(post_id)
      .then(response => {
        console.log("API:SET_FIND_CHAT_LOGS  chatlogs   ", response.data);
        commit('SET_FIND_CHAT_LOGS', response.data);
      })
      .catch(error => {
        console.log(" chatlogs   ", error);
      })
  },

  POST_CHAT_DATA(c, chatData) {
    return postChatData(chatData)
      .then(response => {
        console.log("  ", response.data);
        store.dispatch('FIND_CHAT_LOGS', chatData.post_id)
      })
      .catch(error => {
        console.log("  ", error);
      })
  },

  ////////////////////////// ROOMINFO //////////////////////////

  // 
  JOIN_DELIVERY_POST(content, roomInfo) {
    return postRoomInfo(roomInfo)
      .then(response => {
        console.log('API:JOIN_DELIVERY_POST\n  ', response);
        store.dispatch("FIND_POST_ID", store.state.member.nick);
      })
      .catch(error => {
        console.log('API:JOIN_DELIVERY_POST\n  ', error);
      })
  },

  //  
  EXIT_DELIVERY_POST(content, roomInfo) {
    return patchRoomInfo(roomInfo)
      .then(response => {
        console.log('API:EXIT_DELIVERY_POST\n  ', response);
        store.dispatch("FIND_POST_ID", store.state.member.nick);
      })
      .catch(error => {
        console.log('API:EXIT_DELIVERY_POST\n  ', error);
      })
  },
}